
#footer{
	position:absolute;
	background-color: #515151;
	padding: 0px 0px 15px;	
	text-align: center;
	color: #fff;
	margin-top: 300px;
	width: 1200px;
}
.footer img {
	margin: 0px;
}
.footer .footer-menu {
/*	margin: 20px auto;*/
}
.site-footer-links {
	padding-left: 0px;
}
.footer .footer-menu li {
	display: inline-block;
	display: inline;
	zoom: 1;
	margin-right: 10px
}
.footer .footer-menu a {
	color: #fff;
}
.footer .footer-menu a:hover {
	color: #ffa955;
}
.footer .copyright {
	color: #efefef;
}
.footer .copyright a {
	color: #efefef;
}
.footer .copyright a:hover {
	color: #ffa955;
}



